import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import * as auth from './services/auth_service';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./views/authentication/Login.vue'),
      beforeEnter(to, from, next) {
        if(!auth.isLoggedIn()) {
          next();
        } else {
          next('/');
        }
      }
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('./views/authentication/Register.vue'),
      beforeEnter(to, from, next) {
        if(!auth.isLoggedIn()) {
          next();
        } else {
          next('/');
        }
      }
    },
    {
      path: '/reset-password',
      name: 'reset-password',
      component: () => import('./views/authentication/ResetPassword.vue')
    },
    {
      path: '/forgot',
      name: 'forgot',
      component: () => import('./views/authentication/forgot.vue')
    },
    {
      path: '/meter-owner',
      name: 'meter-owner',
      component: () => import('./views/meter_owner/Dashboard.vue'),
      beforeEnter(to, from, next) {
        if(auth.isLoggedIn() && auth.getUserAccountType() === 1315) {
          next();
        } else {
          next('/404');
        }
      }
    },
    {
      path: '/estate-admin',
      name: 'estate-admin',
      component: () => import('./views/estate_admin/Dashboard.vue'),
      beforeEnter(to, from, next) {
        if(auth.isLoggedIn() && auth.getUserAccountType() === 51) {
          next();
        } else {
          next('/404');
        }
      }
    },
    {
      path: '/new-estate',
      name: 'new-estate',
      component: () => import('./views/estate_admin/NewEstate.vue')
    },
    {
      path: '/add-meter',
      name: 'add-meter',
      component: () => import('./views/estate_admin/AddMeter.vue')
    },
    {
      path: '/add-bulk',
      name: 'add-bulk',
      component: () => import('./views/estate_admin/AddBulk.vue')
    },
    {
      path: '/estates',
      name: 'estates',
      component: () => import('./views/estate_admin/Estates.vue')
    },
    {
      path: '/estate-statistics',
      name: 'estate-statistics',
      component: () => import('./views/estate_admin/EstateStatistics.vue')
    },
    {
      path: '/offline',
      name: 'offline',
      component: () => import('./views/offline/offline.vue')
    },
    {
      path: '/anonymous',
      name: 'anonymous',
      component: () => import('./views/anonym/Anonymous.vue')
    },
    {
      path: '*',
      component: () => import('./views/404.vue')
    }
  ],
  linkActiveClass: 'active'
})
