import store from '../store';
import { http, httpFile } from './http_service'; 
import jwt from 'jsonwebtoken';

export function login(user) {
    return http().post('/login', user)
    .then(res => {
        if (res) {
            setToken(res.data.token, res.data.user);
        }
    });
}

function setToken(token, user) {
    localStorage.setItem('vendr-softgear2019', token);
    store.dispatch('authenticate', user);
}

export function isLoggedIn() {
    const token = localStorage.getItem('vendr-softgear2019');
    return token != null;
}

export function getUserEmail() {
    const token = decodeToken();
    if (!token) {
        return null;
    }
    return token.user.email;
}

export function getUserPhoneNumber() {
    const token = decodeToken();
    if (!token) {
        return null;
    }
    return token.user.phoneNumber;
}

export function getUserId() {
    const token = decodeToken();
    if (!token) {
        return null;
    }
    return token.user.id;
}

export function getUserAccountType() {
    const token = decodeToken();
    if (!token) {
        return null;
    }
    return token.user.accountType;
}

export function registerUser(user) {
    return http().post('/register', user);
}

export function resetPassword(user) {
   return http().post('/reset-password', user);
}

export function forgot(user) {
    return http().post('/forgot', user);
 }

function decodeToken() {
    const token = getToken();
    if (!token) {
        return null;
    }
    return jwt.decode(token); 
}

export function getUserById() {
    const id = getUserId();
    return http().get(`/profile/${id}`);
}

export function getToken() {
    return localStorage.getItem('vendr-softgear2019');
}

export function logout() {
    localStorage.removeItem('vendr-softgear2019');
    store.dispatch('authenticate');
}

