<script>
import * as meter from '../services/meter_service';
export default {
  name: "home",
  data() {
    return {
      meterNumber: "",
      errors: []
    };
  },
  methods: {
    findMeterNumber: async function() {
     // console.log("FInding Meters! "+this.meterNumber.length)
      //this.errors = [];
      if (this.meterNumber.length < 5) {
        this.flashMessage.warning({
            title: "Meter Number",
            message:
              "Please check your meter number",
            time: 5000,
            icon: "/img/user-dummy.52069854.jpg"
          });
        return;
      }

      try {
        const { data } = await meter.findMeterNumber(this.meterNumber);
        //console.log(data.meter )
        
        this.$router.push({ name: 'anonymous',params:{meterData:data.meter}});
        // return data
        
      } catch (error) {
        console.log(error.response.data)
        if (error && error.response.data.notFound) {
          this.flashMessage.warning({
            title: "We are sorry",
            message:
              "Meter number "+this.meterNumber+" was not found",
            time: 5000,
            icon: "/img/user-dummy.52069854.jpg"
          });
        } else {
          this.flashMessage.error({
          title: "Not Found",
          message:
            "Some error occured during fetching your Meter",
          time: 5000,
          icon: "/img/user-dummy.52069854.jpg"
        });
        }
        
      }
    }
  }
};
</script>
