import { http } from './http_service'; 

export function getOwnerMeters() {
    return http().get(`/owner_meters/`);//continue to Meter_routes
}

export function getLoggedInUser(){
    return http().get(`/owner_meters_details/`);//co
}

export function findMeterNumber(meterNumber) {
    return http().get(`/find_meters/${meterNumber}`);

}

export function generateTokenWater(tokenData) {
    return http().post('/generate_token_water', tokenData);
}

export function verifyElectrictyMeter(credentials) {
    return http().post('/verify_electricty_meter', credentials);
}

export function generateTokenElectricity(tokenData) {
    return http().post('/generate_token_electricity', tokenData);
}