<template>
  <div id="app">
    <Navbar v-bind:scrollTopValue="scrollTopValue" />
    <div class="main-wrapper">
      <router-view/>
    </div>
    <Footer />

    <FlashMessage></FlashMessage>
  </div>
</template>

<script>
  import Navbar from '@/components/Navbar.vue';
  import Footer from '@/components/Footer.vue';
  import * as auth from './services/auth_service';
  export default {
    name: 'app',
    components: {
      Navbar,
      Footer
    },
    beforeCreate: async function() {
      if (auth.isLoggedIn()) {
        await auth.getUserById()
        .then(response => {
          this.$store.dispatch('authenticate', response.data.profile);
        })
        .catch(error => {
          auth.logout();
        });
      }
    },
    data() {
      return {
        scrollTopValue: 0
      }
    },
    methods: {
      windowOnScroll() {
        this.scrollTopValue = window.scrollY;
      }
    },
    created() {
      window.addEventListener('scroll', this.windowOnScroll);
    }
  }
</script>

