<template>
    <header :class="{'fixed-header': scrollTopValue >= 50 || openNav}">
        <nav class="navbar header-nav navbar-expand-lg">
            <div class="container container-large">
                <router-link class="navbar-brand" to="/">
                    <img class="light-logo" src="../assets/static/img/logo-light.svg" title="" alt="">
                    <img class="dark-logo" src="../assets/static/img/logo.svg" title="" alt="">
                </router-link>

                <button v-on:click="setClass" class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
                    <span></span>
                    <span></span>
                    <span></span>
                </button>

                <div class="collapse navbar-collapse justify-content-end" id="navbar">
                    <ul class="navbar-nav ml-auto align-items-center">
                        <li><router-link class="nav-link" to="/" exact>Home</router-link></li>
                        <li v-if="$store.state.isLoggedIn">
                            <router-link :to="`${$store.state.userAccountType === 51 ? 'estate-admin' : 'meter-owner'}`" class="nav-link">Dashboard</router-link>
                        </li>
                        <li v-if="$store.state.isLoggedIn && $store.state.userAccountType === 51">
                            <router-link :to="`estates`" class="nav-link">Estates</router-link>
                        </li>
                        <li v-if="$store.state.isLoggedIn">
                            <div class="btn-group all-radius">
                                <button type="button" class="nav-link-btn btn btn-header m-25px-l md-m-0px-l py-2 all-radius" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    {{$store.state.profile.firstName}} <span class="ti-angle-down"></span>
                                </button>
                                <div class="dropdown-menu dropdown-menu-right">
                                    <button v-on:click="logout" class="dropdown-item">Logout</button>
                                </div>
                            </div>
                        </li>
                        <li v-else><router-link to="login" class="nav-link-btn btn btn-header m-25px-l md-m-0px-l py-2">Sign In / Register</router-link></li>
                    </ul>
                </div>
            </div>
        </nav>
    </header>
</template>

<script>
    import * as auth from '../services/auth_service';
    export default {
        name: 'navbar',
        props: ['scrollTopValue'], 
        data() {
            return {
                openNav: false,
            }
        },
        methods: {
            setClass() {
                this.openNav = !this.openNav;
            },
            logout: function() {
                auth.logout();
				this.$router.push({ name: 'login' });
            }
        }
    }
</script>

